  .containerr {
    background-color: #ffffff;
    width: 100vmin;
    padding: 50px 30px;
    position: absolute;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    left: 50%;
    top: 50%;
    border-radius: 10px;
    box-shadow: 0 25px 50px rgba(7, 20, 35, 0.2);
  }
  .options {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    grid-gap: 15px;
    gap: 15px;
  }
  .buttonW {
    height: 28px;
    width: 28px;
    display: grid;
    place-items: center;
    border-radius: 3px;
    border: none;
    background-color: #ffffff;
    outline: none;
    color: #020929;
  }
  select {
    padding: 7px;
    border: 1px solid #020929;
    border-radius: 3px;
  }
  .options label,
  .options select {
    font-family: "Poppins", sans-serif;
  }
  .input-wrapper {
    display: flex;
    align-items: center;
    grid-gap: 8px;
    gap: 8px;
  }
  input[type="color"] {
    -webkit-appearance: none;
    appearance: none;
    background-color: transparent;
    width: 40px;
    height: 28px;
    border: none;
    cursor: pointer;
  }
  input[type="color"]::-webkit-color-swatch {
    border-radius: 15px;
    box-shadow: 0 0 0 2px #ffffff, 0 0 0 3px #020929;
  }
  input[type="color"]::-moz-color-swatch {
    border-radius: 15px;
    box-shadow: 0 0 0 2px #ffffff, 0 0 0 3px #020929;
  }
  #text-input {
    margin-top: 10px;
    border: 1px solid #dddddd;
    padding: 20px;
    height: 50vh;
  }
  .editor {
    background-color: rgb(237, 244, 246);
    width: auto;
    height: 400px;
  }

  .hide{
    display: none;
  }

.Search{
    display: flex;
    align-items: center;
    flex-direction: column;
}

.searchInputs {
    margin-top: 105px;
    display: flex;
}

.Search input {
    background-color: white;
    border: 1px solid;
    border-radius: 5px;
    font-size: 18px;
    padding: 15px;
    height: 30px;
    width: 300px;
}

.icon{
    margin-right: 8px;
}

.dataResult {
    position: absolute;
    z-index: 99;
    border-radius: 5px;
    margin-top: 35px;
    width: 300px;
    height: 400px;
    background-color: white;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    overflow: hidden;
    overflow-y: auto;
}

.dataResult::-webkit-scrollbar {
    display: none;
}

.dataResult .dataItem {
    width: 100%;
    height: 60px;
    display: flex;
    align-items: center;
    color: black;
    text-decoration: none;
    padding: 10px;
    border-bottom: 1px solid;
    justify-content: flex-start;
    flex-direction: column;
}

.dataItem .singleItem {
    margin-left: 10px;
}

.dataItem:hover {
    background-color: gray;
}

#clearBtn {
    cursor: pointer;
}
.contact-form {
  background-color: white;
  margin-top: 5%;
  margin-bottom: 5%;
  width: 70%;
}

.contact-form .form-control {
  border-radius: 1rem;
}

.contact-form form {
  padding: 14%;
}

.contact-form form .row {
  margin-bottom: -7%;
}

.contact-form h2 {
  margin-bottom: 5%;
  margin-top: -15%;
  text-align: center;
  color: #0062cc;
}

.contact-form .btnContact {
  width: 50%;
  border: none;
  border-radius: 1rem;
  padding: 1.5%;
  background: #dc3545;
  font-weight: 600;
  color: #fff;
  cursor: pointer;
}

.btnContactSubmit {
  width: 50%;
  border-radius: 1rem;
  padding: 1.5%;
  color: #fff;
  background-color: #0062cc;
  border: none;
  cursor: pointer;
}

.form-group{
  margin-top: 16px;
  margin-bottom: 16px;
}

.bgcolor{
  background-color: rgb(232, 250, 255);
}
